import React, { Component } from 'react';
import { Button, Form, FormGroup, FormText, Input, Label } from 'reactstrap';
class JumbotronForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      connected: false,
      open: false
    };
    this.socket = new WebSocket('ws://localhost:8080');
    this.socket.onopen = () => {
      console.log('a socket has been opened');
      this.setState({
        connected: true
      });
    };
    this.emit = this.emit.bind(this);
  }
  emit() {
    if (this.state.connected) {
      this.socket.send('It worked!');
      this.setState((prevState) => {
        return {
          open: !prevState.open
        };
      });
    }
  }
  handleFormSubmit(e) {
    e.preventDefault();
    const text = this.refs.messageText.value;
    this.socket.send(text);
  }
  componentDidMount() {
    this.socket.onopen = () => {
      return this.socket.send(JSON.stringify({
        payload: 'Hello Mr. Server!',
        type: 'greet'
      }));
    };
    this.socket.onmessage = ({
      data
    }) => {
      return console.log(data);
    };
  }
  render() {
    return (
      <Form>
        <FormGroup>
          <Label for="exampleEmail">Search</Label>
          <Input type="email" name="email" id="exampleEmail" refs="messageText" placeholder="Choose news feed to add..." />
        </FormGroup>
        <Button color="primary" type="submit">
        </Button>
      </Form>
    );
  }
}
export default JumbotronForm;



